let map;

function initMap() {
  const uluru = { lat: 46.42391, lng: 30.724514 };
  map = new google.maps.Map(document.getElementById("map"), {
    zoom: 17,
    center: uluru,
    disableDefaultUI: true,
    styles: [
      /* ваш код стилей */
    ],
  });
  const marker = new google.maps.Marker({
    position: uluru,
    map: map,
  });
}

window.initMap = initMap;

const mapObserver = new IntersectionObserver((entries, observer) => {
  entries.forEach(entry => {
    if (entry.isIntersecting) {
      observer.unobserve(entry.target);
      const script = document.createElement('script');
      script.src = "https://maps.googleapis.com/maps/api/js?key=AIzaSyAgXxhu9C81FFg0mKBFIY2n4LLxYeiOqpA&callback=initMap&v=weekly";
      script.defer = true;
      document.body.appendChild(script);
    }
  });
});

mapObserver.observe(document.getElementById('map'));